<!-- client/src/components/Game/GameStatus.vue -->
<template>
  <div class="game-status">
    <div v-if="isObserver" class="observer-message">
      <p>{{ observerMessage || 'You are currently observing this game.' }}</p>
      <button v-if="currentGame && currentGame.status === 'waiting'" @click="joinGame" class="join-btn">
        Join Game
      </button>
    </div>
    <div v-if="currentGame.status === 'waiting'" class="waiting-status">
      <p>Waiting for players to join...</p>
      <p>Game ID: <strong>{{ gameId }}</strong></p>
      <p>Players: {{ currentGame.players.length }}/8</p>

      <!-- Player ready component -->
      <PlayerReadyComponent :currentGame="currentGame" :currentUser="currentUser" :gameId="gameId"
        :isCreator="isCreator" @addToLog="addToLog" @playersReady="onPlayersReady" />

      <!-- Prominent game creator section -->
      <div class="creator-info" v-if="isCreator">
        <div class="creator-badge">
          You are the game creator
        </div>

        <div class="player-count-info">
          <p v-if="currentGame.players.length >= 2" class="ready-message">
            <span v-if="enoughPlayersReady"> Enough players are ready to start the game!</span>
            <span v-else> Waiting for players to mark themselves as ready...</span>
          </p>
          <p v-else class="waiting-message">
             Waiting for more players to join (need at least 2)
          </p>
        </div>

        <div v-if="currentGame.players.length >= 2" class="start-game-container">
          <button @click="startGame" class="start-btn" :disabled="isStarting || !enoughPlayersReady">
            {{ isStarting ? 'Starting...' : 'START GAME' }}
          </button>
        </div>

        <div v-else class="start-game-container">
          <button class="start-btn disabled" disabled>
            Need More Players
          </button>
        </div>
      </div>

      <!-- Non-creator waiting message -->
      <div v-else class="waiting-message">
        <p>Waiting for game creator to start the game...</p>
      </div>
    </div>

    <!-- Rest of the component remains the same -->
    <div v-else-if="currentGame.status === 'active'" class="active-status">
      <!-- Active game content (unchanged) -->
      <p>Game in progress</p>
      <div class="game-info">
        <div class="info-item">
          <span class="label">Current turn:</span>
          <span class="value">{{ getCurrentPlayerName() }}</span>
        </div>
        <div class="info-item">
          <span class="label">Current bet:</span>
          <span class="value">{{ currentGame.currentBet }} chips</span>
        </div>
        <div class="info-item">
          <span class="label">Betting round:</span>
          <span class="value">{{ formatBettingRound(currentGame.bettingRound) }}</span>
        </div>
      </div>

      <!-- If game is active but not initialized for this user -->
      <div v-if="isCreator && !gameInitialized" class="initialization-message">
        <p>Game is active but not fully initialized.</p>
        <button @click="requestInitialization" class="initialize-btn">
          Initialize Game
        </button>
      </div>
    </div>

    <div v-else-if="currentGame.status === 'completed'" class="completed-status">
      <p>{{ getCompletionMessage() }}</p>

      <!-- Only show "Return to Lobby" for players with zero balance -->
      <div v-if="hasZeroBalance()">
        <p class="error-message">You don't have enough chips to continue!</p>
        <button @click="returnToLobby" class="btn">
          Return to Lobby
        </button>
      </div>

      <!-- Show "Continue watching" option for observers -->
      <div v-else-if="isObserver">
        <p>You can continue watching or return to the lobby.</p>
        <button @click="returnToLobby" class="btn">
          Return to Lobby
        </button>
      </div>

      <!-- Show waiting message for players who can still play -->
      <div v-else>
        <p>Waiting for the game to start again...</p>
        <p class="status-message">Players without chips have been removed.</p>

        <!-- Ready button -->
        <div class="ready-status-container">
          <button @click="toggleReady" class="ready-btn" :class="{ 'ready-confirm': isCurrentPlayerReady }">
            {{ isCurrentPlayerReady ? 'I\'m Ready ' : 'Mark as Ready' }}
          </button>
        </div>
      </div>
    </div>

    <div v-else class="completed-status">
      <p>Game completed</p>
      <button @click="returnToLobby" class="btn">
        Return to Lobby
      </button>
    </div>

    <!-- Debug info always visible during development -->
    <div class="debug-section" v-if="isDevelopment">
      <button @click="debugVisible = !debugVisible" class="debug-toggle">
        {{ debugVisible ? 'Hide Debug Info' : 'Show Debug Info' }}
      </button>

      <div v-if="debugVisible" class="debug-details">
        <div class="debug-item">
          <span class="debug-label">Creator:</span>
          <span class="debug-value">{{ getCreatorUsername() }}</span>
        </div>
        <div class="debug-item">
          <span class="debug-label">Is Creator:</span>
          <span class="debug-value">{{ isCreator }}</span>
        </div>
        <div class="debug-item">
          <span class="debug-label">Players:</span>
          <span class="debug-value">{{ currentGame.players.length }}</span>
        </div>
        <div class="debug-item">
          <span class="debug-label">Ready Players:</span>
          <span class="debug-value">{{ readyPlayersCount }}</span>
        </div>
        <div class="debug-item">
          <span class="debug-label">Enough Ready:</span>
          <span class="debug-value">{{ enoughPlayersReady }}</span>
        </div>
        <div class="debug-item">
          <span class="debug-label">Status:</span>
          <span class="debug-value">{{ currentGame.status }}</span>
        </div>
        <button @click="forceStartGame" class="debug-btn">Force Start Game</button>
        <button @click="requestStateUpdate" class="debug-btn">Request Game Update</button>
        <button @click="checkReadyPlayers" class="debug-btn">Check Ready Players</button>
      </div>
    </div>
  </div>
</template>

<script>
import PlayerReadyComponent from './PlayerReadyComponent.vue';

export default {
  name: 'GameStatus',

  components: {
    PlayerReadyComponent
  },

  props: {
    currentGame: {
      type: Object,
      required: true
    },
    currentUser: {
      type: Object,
      default: null
    },
    gameId: {
      type: String,
      required: true
    },
    isCreator: {
      type: Boolean,
      default: false
    },
    isStarting: {
      type: Boolean,
      default: false
    },
    gameInitialized: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      debugVisible: false,
      isDevelopment: process.env.NODE_ENV !== 'production',
      enoughPlayersReady: false
    };
  },

  computed: {
    readyPlayersCount() {
      if (!this.currentGame || !this.currentGame.players) return 0;
      return this.currentGame.players.filter(p => p.isReady).length;
    }
  },

  watch: {
    // This watcher ensures that enoughPlayersReady gets updated when the game state changes
    currentGame: {
      handler(newGame) {
        this.checkReadyPlayers();
      },
      deep: true
    }
  },

  mounted() {
    // Check ready players on component mount
    this.checkReadyPlayers();
  },

  methods: {
    /**
     * Get appropriate completion message based on game state
     */
    getCompletionMessage() {
      // If message is about "Chip e entek", return that message
      const endMessage = this.findCompletionMessage();
      if (endMessage && endMessage.includes("Chip e entek")) {
        return "Game ended: Chip e entek";
      }

      // Default message
      return "Game completed";
    },

    /**
     * Find the completion message in action history if available
     */
    findCompletionMessage() {
      if (!this.currentGame || !this.currentGame.actionHistory) {
        return null;
      }

      // Look for the gameCompleted action in the history
      const completionAction = this.currentGame.actionHistory
        .filter(action => action.action === 'gameCompleted')
        .pop();

      return completionAction ? completionAction.message : null;
    },

    /**
     * Check if current player has zero balance
     */
    hasZeroBalance() {
      if (!this.currentUser || !this.currentGame || !this.currentGame.players) {
        return false;
      }

      const currentPlayer = this.currentGame.players.find(
        p => p.id === this.currentUser.id
      );

      return currentPlayer && (currentPlayer.totalChips <= 0);
    },

    /**
     * Toggle player ready status
     */
    toggleReady() {
      if (!this.currentUser || !this.gameId) {
        console.error('Cannot toggle ready: missing user or game data');
        return;
      }

      SocketService.setPlayerReady(
        this.gameId,
        this.currentUser.id,
        !this.isCurrentPlayerReady
      );

      this.$emit('addToLog', `You marked yourself as ${!this.isCurrentPlayerReady ? 'ready' : 'not ready'}`);
    },

    getCurrentPlayerName() {
      if (!this.currentGame || !this.currentGame.currentTurn) return 'N/A';

      // Find player by ID, avoiding circular structure
      const player = this.currentGame.players.find(
        p => p.id === this.currentGame.currentTurn
      );

      return player ? player.username : 'Unknown';
    },

    getCreatorUsername() {
      return this.currentGame.creator?.username || 'Unknown';
    },

    formatBettingRound(round) {
      const formats = {
        'preflop': 'Pre-Flop',
        'flop': 'Flop',
        'turn': 'Turn',
        'river': 'River',
        'showdown': 'Showdown'
      };
      return formats[round] || round;
    },

    startGame() {
      console.log("Start game button clicked");
      if (!this.enoughPlayersReady) {
        console.warn("Cannot start game - not enough players are ready");
        this.addToLog("Cannot start game - need at least 2 ready players");
        return;
      }
      this.$emit('startGame');
    },

    forceStartGame() {
      console.log("Forcing game start from debug panel");
      this.$emit('startGame');
    },

    requestInitialization() {
      this.$emit('requestInitialization');
    },

    requestStateUpdate() {
      this.$emit('requestStateUpdate');
    },

    returnToLobby() {
      this.$router.push('/lobby');
    },

    onPlayersReady(isReady) {
      this.enoughPlayersReady = isReady;
      console.log("Players ready status updated:", isReady);
    },

    addToLog(message) {
      this.$emit('addToLog', message);
    },

    checkReadyPlayers() {
      if (!this.currentGame || !this.currentGame.players) {
        this.enoughPlayersReady = false;
        return;
      }

      const readyPlayers = this.currentGame.players.filter(p => p.isReady);
      const enoughReady = readyPlayers.length >= 2;

      console.log(`Ready players check: ${readyPlayers.length} ready, enough: ${enoughReady}`);

      // Update the internal state
      this.enoughPlayersReady = enoughReady;

      // Also emit the event for parent components
      this.$emit('playersReady', enoughReady);
    }
  }
};
</script>

<style scoped>
.game-status {
  grid-area: status;
  background-color: #2a2a2a;
  padding: 15px;
  border-radius: 8px;
  text-align: center;
}

.waiting-status,
.active-status,
.completed-status {
  margin-bottom: 20px;
}

.game-info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: #333;
  border-radius: 6px;
  padding: 12px;
  margin: 10px auto;
  max-width: 300px;
}

.info-item {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 8px;
}

.label {
  color: #999;
  font-weight: bold;
}

.value {
  color: #fff;
}

.creator-info {
  margin-top: 20px;
  padding: 15px;
  background-color: rgba(63, 140, 110, 0.15);
  border-radius: 8px;
  border: 2px dashed #3f8c6e;
}

.creator-badge {
  background-color: #3f8c6e;
  color: white;
  padding: 6px 10px;
  border-radius: 20px;
  display: inline-block;
  margin-bottom: 15px;
  font-weight: bold;
}

.player-count-info {
  margin-bottom: 15px;
}

.ready-message {
  color: #4caf50;
  font-weight: bold;
  font-size: 16px;
}

.waiting-message {
  margin-top: 20px;
  font-style: italic;
  color: #aaa;
}

.initialization-message {
  margin-top: 20px;
  padding: 10px;
  background-color: #554400;
  border-radius: 8px;
  border: 1px solid #ffc107;
}

.start-game-container {
  margin-top: 20px;
}

.start-btn,
.initialize-btn {
  padding: 12px 24px;
  border: none;
  border-radius: 4px;
  background-color: #4caf50;
  color: white;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
  animation: pulse 2s infinite;
}

.initialize-btn {
  background-color: #ff9800;
  animation: none;
}

.initialize-btn:hover {
  background-color: #e68900;
}

.start-btn:hover {
  background-color: #3d8b40;
}

.start-btn.disabled {
  background-color: #555;
  cursor: not-allowed;
  animation: none;
}

.start-btn:disabled {
  background-color: #555;
  cursor: not-allowed;
  animation: none;
}

@keyframes pulse {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(76, 175, 80, 0.7);
  }

  70% {
    transform: scale(1.05);
    box-shadow: 0 0 0 10px rgba(76, 175, 80, 0);
  }

  100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(76, 175, 80, 0);
  }
}

.debug-section {
  margin-top: 20px;
  border-top: 1px dashed #555;
  padding-top: 15px;
  text-align: left;
}

.debug-toggle {
  background-color: #333;
  color: #aaa;
  border: 1px solid #555;
  border-radius: 4px;
  padding: 5px 10px;
  font-size: 12px;
  cursor: pointer;
}

.debug-details {
  margin-top: 10px;
  padding: 10px;
  background-color: #333;
  border-radius: 4px;
  font-family: monospace;
  font-size: 12px;
}

.debug-item {
  margin-bottom: 5px;
}

.debug-label {
  display: inline-block;
  width: 80px;
  color: #aaa;
}

.debug-value {
  color: #fff;
  word-break: break-all;
}

.debug-btn {
  background-color: #ff9800;
  color: black;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
  margin-top: 10px;
  margin-right: 5px;
  cursor: pointer;
  font-size: 12px;
}

.debug-btn:hover {
  background-color: #e68900;
}

.observer-message {
  background-color: #ffcc00;
  color: #333;
  padding: 10px;
  margin-bottom: 15px;
  border-radius: 4px;
  text-align: center;
}

.join-btn {
  background-color: #3f8c6e;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px 15px;
  margin-top: 5px;
  cursor: pointer;
  font-weight: bold;
}

.join-btn:hover {
  background-color: #2c664e;
}

.error-message {
  color: #e74c3c;
  font-weight: bold;
  margin: 10px 0;
}

.status-message {
  color: #f39c12;
  font-style: italic;
  margin: 10px 0;
}

.ready-btn {
  background-color: #555;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-top: 15px;
}

.ready-btn.ready-confirm {
  background-color: #4caf50;
}

.ready-btn:hover {
  background-color: #666;
}

.ready-btn.ready-confirm:hover {
  background-color: #3d8b40;
}
</style>